@import "../../scss/variables";

.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 2rem;

    .cards{
        // border: 2px solid red;
        width: 500px;
        height: 350px;
        position: relative;
        overflow: hidden;
        &:hover .card-over{
            transform: translate(0%);
        }
        &:hover img{
            opacity: 1;
        }
            

        .title{
          position: absolute;
          top: 0;
          left: 0;
          background-color: rgba(0, 0, 0, 0.8);
          color: $cardNavBgColor;
          padding: 1rem;
          border-radius: 10px 20px 20px 20px ;
          z-index: 2;
        }
        .date{
           position: absolute;
           top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          background-color: rgba(0, 0, 0, 0.8);
          color: $cardNavBgColor;
          padding: 1rem;
          font-size: 0.3rem;
          border-radius: 10px;
          z-index: 2;
        }
    }
}

img{
    width: 500px;
    height: 350px;
    object-position: cover;
    border-radius: 10px;
    opacity: 0.5;
}

.card-over{
    width: 500px;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color:rgba(0, 0, 0, 0.8) ;
    font-size: 1.5rem;
    padding: 1rem;
    border-radius: 10px;
    transform: translateY(100%);
    transition: transform 0.7s ease-in-out;
    z-index: 3;
    max-height: 75%;
    text-align: justify;
    overflow: auto;

    &::-webkit-scrollbar{
        display: none;
    }
 }